@tailwind base;
@tailwind components;
@tailwind utilities;



.video-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* 2 columns for big screens */
    gap: 20px; /* Remove the gap between the videos */
    width: 100%; /* Ensure the container takes the full width */
    margin: 0; /* Remove any margin */
    padding: 0; /* Remove padding */
}

.video-wrapper {
    position: relative;
    width: 100%;
    padding-top: 56.25%; /* 16:9 Aspect Ratio */
    overflow: hidden;
    border-radius: 4px; /* Add border radius */
    transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth transition for hover effects */
}

.video-wrapper:hover {
    transform: scale(1.001); /* Slightly scale up on hover */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Add shadow effect on hover */
}

.video-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
@media (max-width: 768px) {
  .video-grid {
    grid-template-columns: 1fr;
    padding: 0;
    margin: 0;
  }

  .video-grid iframe,
  .video-grid video,
  .video-grid img {
    width: 100vw;           /* Full screen width */
    height: 280px;
    object-fit: cover;
    border-radius: 0;       /* No rounded edges if you want full edge-to-edge */
    margin-left: -16px;     /* Optional: adjust to match container padding */
    margin-right: -16px;
  }
}
